
import Confirm from 'vux/src/components/confirm/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Uploader from 'vux-uploader'
export default {
directives: {
TransferDom
},
data () {
return {
images: [
{url: 'http://www.xiaoxiangba.com/assets/img/smile.png'}
],
uploadUrl: {
result: 0,
message: "result不是0时候的错误信息",
data: {
url: "http://www.xiaoxiangba.com/assets/img/smile.png"
}
},
userInfo: [],
myImage: '../../../src/assets/img/defaultImg.png',
value1: '2015-11-12',
value3: ['广东省', '中山市', '--'],
addressData: ChinaAddressV4Data,
show1: false,
show2: false,
show3: false,
show4: false,
show5: false,
show6: false,
nickname: '用户3852',
sex: '请输入您的性别',
birthday: '请输入你的生日',
phone: '15622633852',
email: '添加邮箱',
address: '请修改你的地址',
temperature: '请修改你的温度',
menus1: {},
menus2: {
menu1: '从相册上传'
},
menus4: {
menu1: '男',
menu2: '女'
},
isLogouting: false,
}
},
created() {
var mydata = JSON.parse(localStorage.mydata).user
this.nickname = mydata.nickname
this.phone = mydata.phone
this.sex = mydata.sex
this.email = mydata.email
this.address = mydata.address
this.birthday = mydata.birthday
this.myImage = 'http://120.79.21.193/SmartHome' + mydata.photo
},
methods: {
addImageMethod(value) {
console.log(value)
},
clickSubmit() {
var mydata = JSON.parse(localStorage.mydata).user
this.$vux.alert.show({})
this.$axios.post('/updateUserInfo', {
id: mydata.id,
nickname: this.nickname,
sex: this.sex,
birthday: this.birthday,
phone: this.phone,
email: this.email,
address: this.address
})
.then((res) => {



console.log(res)
})
},
clickChange() {
this.$router.push('/register')
},
clickAddress(value) {
this.value3 = !this.value3
},
clickAddressName(id, name){
this.address = name.join(' ')
},
clickEmail() {
var that = this
this.$vux.confirm.prompt('请输入你的邮箱', {
onConfirm (value) {
that.email = value
}
})
},
clickPhone() {
var that = this
this.$vux.confirm.prompt('请输入你的电话', {
onConfirm (value) {
that.phone = value
}
})
},

update (e) {   // 上传照片
var mydata = JSON.parse(localStorage.mydata).user
var self = this
var file = e.target.files[0]
/* eslint-disable no-undef */
var param = new FormData()  
param.append('file', file, file.name)  
param.append('id', mydata.id) 
console.log(param.get('file')) 
var config = {
headers: {'Content-Type': 'multipart/form-data'}
}

self.$axios.post('http://192.168.43.27:8080/SmartHome/uploadUserPhoto', param, config)
.then(res => {
localStorage.setItem('mydata', JSON.stringify(res.data[0]))
self.myImage = 'http://192.168.43.27:8080/SmartHome' + res.data[0].user.photo
})
},
clickBrithday (value) {
console.log('change', value)
this.brithday = value
},
click (key) {
console.log(key)
},
click4 (key, item) {
this.sex = item
},
onConfirm6 (value) {
this.phone = value
},
onConfirm5 (value) {
this.nickname = value
},
clickNickname() {
this.show5 = !this.show5
},
clickSex() {
this.show4 = !this.show4
},
clickUpload() {
this.update(e);


},
clickChoose() {
this.show2 = !this.show2
this.addImageMethod()
},
clickLogout(){

this.delCookie('session');
localStorage.removeItem("mydata");

setTimeout(() => {
this.$axios.post('/logout')
.then((res) => {
console.log(res)
})
this.$router.push('/login');
}, 3000)

this.$http.get('/SmartHome/logout')
.then((res) => {
console.log(res.body)
})
.catch((err) => {
console.log(err)
})
}
},
components: {
Confirm,
Datetime,
Loading,
Actionsheet,
XSwitch,
XButton,
XImg,
Cell,
Group,
XHeader,
XAddress,
ChinaAddressV4Data,
Uploader
}
}



