
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
components: {
XHeader,
XButton,
XInput,
Group
},
data() {
return {
phoneNum: '',
code: '',
password: '',
showGet: true,
isGet: false,
timer: null,
count: '',
isOk: true,
isPwd: 'password',
isShow: true
}
},
created() {
this.clickGetCode()
this.$axios.post('/getCode', {phone: this.phoneNum})
.then((res) => {
console.log(res);
})
this.phoneNum = this.$route.query.phone
},
methods: {
clickGetCode() {
this.code = ''
this.phoneNum = ''
const TIME_COUNT = 60
if (!this.timer) {
this.count = TIME_COUNT
this.showGet = false
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= TIME_COUNT) {
this.count--
} else {
this.showGet = true
clearInterval(this.timer)
this.timer = null
}
}, 1000)
}
},
clickOk() {
console.log(this.phoneNum,this.password);
this.$vux.loading.show()
this.$axios.post('/register', {
phone: this.phoneNum,
password: this.password
})
.then((res) => {
console.log(res)
if(res.data=='registerSuccess'){
setTimeout(() => {
this.$vux.loading.hide()
this.$router.push('/login')
}, 500)
}
})
},
clickShow() {
this.isShow = !this.isShow
if (this.isShow == false) {
this.isPwd = 'text'
} else {
this.isPwd = 'password'
}
}
},
watch: {
code: function () {
if (this.code !== '' && this.password.length >= 6) {
this.isOk = false
} else {
this.isOk = true
}
},
password: function () {
if (this.code !== '' && this.password.length >= 6) {
this.isOk = false
} else {
this.isOk = true
}
}
}
}



