
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
data() {
return {
bgImg: [
'http://www.xiaoxiangba.com/assets/img/bg01.jpg',
'http://www.xiaoxiangba.com/assets/img/bg02.jpg',
'http://www.xiaoxiangba.com/assets/img/bg03.jpg',
'http://www.xiaoxiangba.com/assets/img/bg04.jpg',
'http://www.xiaoxiangba.com/assets/img/bg05.jpg',
],
showDetail: false,
photo: [],
demo01: 0,
showComplete: false,
edit: '编辑',
showSheet: false,
menus: {
menu1: '拍照',
menu2: '从手机相册中选择'
}
}
},
created(){
this.clickInit()
},
methods: {
clickInit(){
var id = JSON.parse(localStorage.mydata).user.id
var familyId = parseInt(this.$route.query.familyId)
this.$axios.get('/get_family?id=' + id)
.then((res) => {
this.data = res.data
})
this.$axios.get('/get_familyimg?userId=' + id + '&familyId=' + familyId)
.then((res) => {
res.data === 'you have not privilege' ? '' : this.photo = res.data
})
},
clickChooseImg(photo){
var id = this.$route.query.familyId;
var userId = JSON.parse(localStorage.mydata).user.id
console.log(id, userId, photo);
this.$axios.post('/ud_family', {
userId: userId,
id: id,
photo: photo
})
.then((res) => {
console.log(res);
this.clickInit()
})
},
clickDetail(id){
this.$axios.post('/del_familyimg ', {
userId: JSON.parse(localStorage.mydata).user.id,
familyId: parseInt(this.$route.query.familyId),
familyImgId: id
})
.then((res) => {
var that = this
this.showDetail = false
setTimeout(function () {
that.clickInit()
}, 500)
this.showDetail = false
})
},
upload (e) {   // 上传照片
var familyId = parseInt(this.$route.query.familyId)
var userId = JSON.parse(localStorage.mydata).user.id
var files = e.target.files
var param = new FormData()
param.append('files', files, files.name)
param.append('userId', userId)
param.append('familyId', familyId)
this.$axios.post('/add_familyimg', param, {
headers: {'Content-Type': 'multipart/form-data'}
})
.then(res => {
this.clickInit();
})
},
consoleIndex() {

},
showEdit() {
this.showComplete = !this.showComplete
},
click(key) {
console.log(key)
}
},
components: {
ButtonTab,
ButtonTabItem,
XHeader,
Actionsheet
}
}



